import { Weapon, WeaponHandedness } from "../model/weapon";

const SPRADSHEET_ID = '1HXS_aF9CKeGKg6OecwCw3WobZWcTjDkp_4ipN1vnyVk';

export default class DataProvider {
    cachingDurationSeconds: number;

    private readonly googleApiKey: string;

    constructor(googleApiKey: string, cachingDurationSeconds = 30 * 60) {
        this.googleApiKey = googleApiKey;
        this.cachingDurationSeconds = cachingDurationSeconds;
    }

    async loadSheetData<T extends SheetsApiResponse>(name: string) {
        const storageKey = `sheet-data-cache-${name}`;
        const cached: {expiresAt: string, data: any} = JSON.parse(localStorage.getItem(storageKey) || 'null');

        if (cached && new Date(cached.expiresAt).getTime() >= new Date().getTime()) {
            console.info('[DataProvider] Using cached data.');
            return <T>cached.data;
        }

        const url = `https://sheets.googleapis.com/v4/spreadsheets/${SPRADSHEET_ID}/values/${name}?key=${this.googleApiKey}`;
        const request = await fetch(url);
        const data  = <T>JSON.parse(await request.text());
        
        if (data.error == null) {
            
            const expiresAt = new Date;
            expiresAt.setSeconds(expiresAt.getSeconds() + this.cachingDurationSeconds);

            localStorage.setItem(storageKey, JSON.stringify({
                data: data,
                expiresAt: expiresAt
            }));
        }

        console.info('[DataProvider] Using fetched data.');
        return data;
    }

    async loadWeapons(): Promise<Weapon[]> {
        const data = await this.loadSheetData<{values: (string[])[]}>('Melee Weapons [Advanced]');
        const result: Weapon[] = [];

        const headerRowCount = 2;

        let rowIndex = -1;
        for (let row of data.values) {
            rowIndex++;

            if (rowIndex < headerRowCount) {
                continue;
            }
            
            result.push(new Weapon(row));
        }

        result.sort(Weapon.sorter);

        return result;
    }
}

export interface SheetsApiResponse {
    error?: {
        code: string;
        message: string;
    }
    values?: any[];
}